/* Media Queries */
html {
  box-sizing: border-box;
  font-size: 62.5%;
  /* 10px = 1rem */
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

section {
  width: min(95%, 120rem);
  margin: 0 auto;
}

.mt {
  margin-top: 25rem;
}
@media (min-width: 768px) {
  .mt {
    margin-top: 18rem;
  }
}

h1, h2 {
  font-family: "PT Sans", sans-serif;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

[class$=__heading] {
  font-family: "PT Sans", sans-serif;
}

[class$=__span] {
  color: #784D3C;
}

.offcanva {
  opacity: 0; /* opacity para trabajar con animaciones */
  height: 0;
  overflow: hidden;
  /* al parecer normalize tiene prioridad en estas propiedades por eso coloco el !important */
  transition-property: opacity !important;
  transition-duration: 500ms !important;
  transition: ease-in-out;
  /* mando a llamar esta clase desde offcanva.js */
  /* mando a llamar esta clase desde offcanva.js */
}
.offcanva__mostrar {
  opacity: 1;
  padding: 2rem;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 2;
}
.offcanva__ocultar {
  opacity: 0;
}
.offcanva__contenedor {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
}
.offcanva__cerrar-menu {
  display: flex;
  justify-content: flex-end;
  padding: 0.7rem 0;
}
.offcanva__enlace {
  font-weight: 700;
  font-size: 3.2rem;
  color: #FFFFFF;
}
.offcanva__enlace:hover {
  color: #cccccc;
}

.header {
  z-index: 1;
  padding: 3rem;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  /* lo mando a llamar desde app.js */
}
@media (min-width: 768px) {
  .header {
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5rem;
    transition: padding 300ms ease-in-out;
  }
}
.header--dark {
  background-color: #000000;
}
.header__heading {
  font-size: 4rem;
  margin: 0;
  color: #FFFFFF;
  letter-spacing: 0.2rem;
}
.header__enlace {
  color: #FFFFFF;
}
.header__enlace:hover {
  color: #cccccc;
}
.header__mobile {
  display: flex;
  justify-content: center;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .header__mobile {
    display: none;
  }
}
.header__tablet {
  display: none;
}
@media (min-width: 768px) {
  .header__tablet {
    display: flex;
    gap: 2rem;
  }
}
.header--moved {
  background-color: rgba(0, 0, 0, 0.75);
  padding: 3rem;
}

.hero {
  background-image: image-set(url("/build/img/banner.jpg") 1x, url("/build/img/banner.webp") 1x);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  min-height: 100vh;
}
.hero__contenido {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #FFFFFF;
  width: min(90%, 80rem);
  margin: 0 auto;
}

/* En este caso la imagen ya es oscura */
.footer {
  margin-top: 5rem;
  padding: 5rem 0;
  color: #FFFFFF;
  background-color: #000000;
  text-align: center;
}
.footer__heading {
  color: #FFFFFF;
}
.footer__sociales {
  display: flex;
  justify-content: center;
  gap: 3rem;
}